.appWrapper {
  display: grid;
  grid-template-areas: 'h h'
                      'n c';
  grid-template-rows: 150px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap:  10px;
  width: 1200px;
  margin: auto;
}
.appWrapperContent {
  grid-area: c;
  background-color: rgb(191, 243, 3);
}

